<template>
    <div class="row" >
      <div class="column" v-for="project in showSix()" :key="project.id">
         <div class="container" > <RouterLink :to="'/project/'+ project.id">
                    <Project 
                    :name="project.name"
                    :description="project.description"
                    :imageName="project.imageName"
                    
                    /></RouterLink>
          </div>
      </div>
  </div>
  <div class="pagebtns">
        <button class="movebtns"  @click="pageBack()"><</button><p class="pagenumber">{{ pageNr }}</p><button class="movebtns" @click="pageForward()" >></button>
      </div>
</template>

<script>
import Project from './Project.vue'
export default{
    name: 'ProjectList',
    components: {
        Project
    },

    data(){
        return{
            projectList:[],
            pageNr: 1
        }
    },

    created() {
        fetch('http://localhost:5152/api/Projects')
        .then(data => data.json())
        .then(projects => {
            this.projectList = projects
        })
    },

    unmounted(){
      console.log("unmounted")
    },
    

    methods: {
        getImagePath(imgName){
            return imgName ? require(`../assets/${imgName}`) : ''
        },
        
        showSix() {
          if(this.pageNr==1){
            return this.projectList.splice(0,6)
          }
          else if(this.pageNr==2){
            
            return this.projectList.splice(6,6)
          }
          else if(this.pageNr==3){
            
            return this.projectList.splice(12,6)
          }
          else if(this.pageNr==4){
           
            return this.projectList.splice(18,3)
          }
        },
        
      pageBack(){
        if(this.pageNr>1){
        this.pageNr--
        fetch('http://localhost:5152/api/Projects')
        .then(data => data.json())
        .then(projects => {
            this.projectList = projects
        })
        }
      },
      pageForward(){
        if(this.pageNr<5) {
          this.pageNr++
          fetch('http://localhost:5152/api/Projects')
        .then(data => data.json())
        .then(projects => {
            this.projectList = projects
        })

        }
      }
    }
}


</script>

<style scoped>
* {
  box-sizing: border-box;
}


.pagenumber{
  color: white;
}

a{
  text-decoration: none;
  color: #2c3e50;
}

a:visited{
  text-decoration: none;
  color: #2c3e50;
}

a:hover{
  text-decoration: none;
  color: #2c3e50;
}

a:focus{
  text-decoration: none;
  color: #2c3e50;
}

a:focus, a:active {
  text-decoration: none;
  color: #2c3e50;
}


.pagebtns{
  display: flex;
  justify-content: center;
}

.movebtns{
  border-radius: 12px;
  border: none;
  padding: 0;
  text-align: center;
  text-decoration:#2c3e50;
  font-size: 16px;
  margin: 15px 2px;
  height: 50px;
  width: 35px;
  background-color: #ca92ed;
}

.movebtns:hover{
  cursor: pointer;

}

.pagenumber{
  font-size: 1.5rem;
  color: #ffffff;
  
}

/* Float four columns side by side */


</style>