<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
import ProjectList from '@/components/ProjectList.vue';

export default {
  name: 'HomeView',
  components: {
    HelloWorld,
    ProjectList
  },

  data(){
    return{
      imageName: "Selfie.png",
      show:true,
      show1:false,
      show2:false,
      enText: true,
      seText:false
    }
  },

  methods: {
        getImagePath(imgName){
            return imgName ? require(`../assets/${imgName}`) : ''
        },

        hideText(shw){
          this.show=!shw
          this.show1=false
          this.show2=false
        },

        hideText1(shw){
          this.show1=!shw
          this.show=false
          this.show2=false
        },
        hideText2(shw){
          this.show2=!shw
          this.show=false
          this.show1=false
        },
        calculateAge(){
        var birthDate= new Date(1995,1,4);
        var dateToday=new Date();
        
        var years = (dateToday.getFullYear() - birthDate.getFullYear());

        if(dateToday.getMonth() < birthDate.getMonth() || dateToday.getMonth() == birthDate.getMonth() && dateToday.getDate() <birthDate.getDate()) {
          years--
          }
          return years;
      },
      changeTextEN(){
        this.enText=true,
        this.seText=false
      },
      changeTextSE(){
        this.seText=true,
        this.enText=false
      }
    }
}
