<template class="single-project">
    <div class="main-container">
        <div class="description-container">
            <h2 class="title">{{ name }}</h2>
            <p class="description">{{ description }}</p>
        </div>
        <span class="image-container">
            <div class="image-group" v-for="image in updatedImgName">
              <img class="result-images" :src="require(`@/assets/results${image}`)" @click="enlarge=!enlarge" alt="code">                
        </div>
        </span > 
    </div>
    <img v-for="(image, index) in updatedImgName" :key="index"  class="enlarged-image" v-show="enlarge" @click="enlarge=!enlarge"  :src="require(`@/assets/results${image}`)" alt="zoomedimage"> 
</template>

<script>



export default{
    data(){
        return{
            name:"",
            description: "",
            imageName:"",
            imageFolder: [],
            updatedImgName: [],
            enlarge: false
        }
    },
    
    created() {
        const id = this.$route.params.id;

        fetch('http://localhost:5152/api/Projects/' + id)
            .then (data => data.json())
            .then(project => {
                this.name = project.name,
                this.description = project.description
                let str = project.imageName
                this.updateArray(this.imageName=str.replace(".png", ""))
            })
    },

    mounted(){
        this.importAll(require.context('../assets/results', true, /[A-Z]\w+\.png$/));
    },
    methods:{
       importAll(r){
        r.keys().forEach(key => (this.imageFolder.push({ pathShort: key })))
       },
       
       updateArray(str){
        var names= this.imageFolder.map(function(item){
            return item['pathShort'];
        })
        for(let name in names){
            let s=names[name]
            if(s.includes(str)){
                var image=s
                this.updatedImgName.push(image.substring(1))
            }
        }
        return this.updatedImgName
        
       },
       
       getImagePath(img){
        return img ? require(`../assets/results${img}`) : ''
        }
    }
}


</script>


<style scoped>
:global(body){
    background-color:#363039;
}

.main-container{
    display: grid;
    grid-template-columns: 25% 75%;
}

.single-project{
    background-color: #363039;
}



.image-container{
    display: flex;
    flex-wrap: wrap;
    gap:5px;  
}
.image-group{
    flex: 33%;
    flex-grow: 0;
}

.result-images{
    height: 100%;
    width: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    background-color: #635e65;
    display: block;
        
}

.result-images:hover{
    opacity: 0.7;
    cursor: pointer;

}
.enlarged-image{
    width: 1000px;
    height: 438px;

}




</style>